export const createSample = (buffer, context, offset = 0) => {
  let sourceNode = null;
  let playing = false;
  let startOffset = offset;

  let startedAt = 0;
  let timeUpdate = () => {};

  const setStart = offset => startOffset = offset;
  let inter;

  const play = (duration = 1) => {
    if (playing) {
      stop();
    }

    sourceNode = context.createBufferSource();
    sourceNode.connect(context.destination);
    sourceNode.buffer = buffer;
    sourceNode.start(0, startOffset, duration);

    startedAt = context.currentTime;

    playing = true;

    inter = setInterval(() => {
      update();
    }, 100);
  }

  const stop = () => {
    clearInterval(inter);

    if (sourceNode) {
      sourceNode.disconnect();
      sourceNode.stop(0);
      sourceNode = null;
    }

    playing = false;
  }

  const onTime = (fn = () => {}) => {
    timeUpdate = fn;
  }

  const getDuration = () => {
    return buffer.duration;
  }

  const getStart = () => {
    return {
      startOffset,
      startPerc: startOffset / getDuration()
    };
  }

  const update = () => {
    let curr = startOffset + (context.currentTime - startedAt);
    timeUpdate(curr, curr / getDuration());
  }

  return {
    play,
    stop,
    setStart,
    getDuration,
    onTime,
    getStart
  }
}

export default class Sound {
  constructor(soundFile, { waveformFidelity = 50 } = {}) {
    this.soundFile = soundFile;
    this.context = new AudioContext();

    this.audioBuffer = false;
    this.waveform = false;

    this.options = {
      waveformFidelity, // How many values in the array of float data
    };
  }

  _getSoundAsBuffer(sound) {
    return fetch(sound)
      .then(resp => resp.arrayBuffer());
  }

  _chopToFloats(buffer, segments = 10) {
    const left = buffer.getChannelData(0);
    const right = buffer.getChannelData(1);

    const section = Math.floor(left.length / segments);

    const data = [];

    for (let i = 0; i < segments; i++) {
      let idx = section * i;
      let float = (((left[idx] + right[idx]) / 2) + 1) / 2; // Normalise to 0-1 of both channels
      data.push(float);
    }

    return Promise.resolve(data);
  }

  analyse() {
    return this._getSoundAsBuffer(this.soundFile)
      // Then create an actual audio buffer from that data
      .then(arrayBuffer => this.context.decodeAudioData(arrayBuffer))
      .then(buffer => {
        this.audioBuffer = buffer;
        return this._chopToFloats(buffer, this.options.waveformFidelity)
      })
      .then(data => {
        this.waveform = data;
        return this.getInfo();
      });
  }

  getInfo() {
    return {
      waveform: this.waveform,
      buffer: this.audioBuffer,
      file: this.soundFile,
    }
  }

  makeSample(start = 0) {
    return createSample(this.audioBuffer, this.context, start);
  }
}
