import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import Circle from './Circle';
import sound from '../sounds/drums.wav';
import Sound from './sound';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

function drawCenterText(txt, fullWidth, y) {
  let { width } = ctx.measureText(txt);
  ctx.fillText(txt, (fullWidth - width) / 2, y);
}

let strs = [
  'DONT USE A BOOK.',
  'YOU GOTTA LEARN IT',
  'ON YOUR OWN'
];

let lineHeight = 50;

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  ctx.font = "bold 24px Oswald";

  ctx.save();
  ctx.translate(0, (height - ((strs.length - 1) * lineHeight)) / 2);
  for (let i = 0; i < strs.length; i++) {
    drawCenterText(strs[i], width, i * lineHeight);
  }
  ctx.restore();

  for (let i = 0; i < circles.length; i++) {
    circles[i].render();
  }
});

const player = new Sound(sound);

let sampler = [];
let circles = [];

player.analyse()
  .then(() => {
    sampler = [
      player.makeSample(0.47), // kick
      player.makeSample(1.1), // snare
      player.makeSample(0.79), // hihat
      player.makeSample(2.6) //openhihat
    ];

    circles = sampler.map(() => {
      return new Circle(WIDTH / 2, HEIGHT / 2, WIDTH * 0.25, 10);
    });

    app.start();
  })

document.body.addEventListener('keyup', (e) => {
  let idx = parseInt(e.key) - 1;

  if (idx < sampler.length) {
    sampler[idx].play(0.25);
    circles[idx].animate();
  }
});
