import { TweenLite } from 'gsap/TweenLite';

export default class Animatable {
  constructor(duration, { onUpdate = () => {}, onComplete = () => {} } = {}) {
    this.defaults = {
      duration: 1,
      start: {
        percentage: 0
      },
      to: {
        percentage: 1
      }
    };

    this.onUpdateFn = onUpdate;
    this.onCompleteFn = onComplete;

    this.options = {
      ...this.defaults,
      duration
    };
  }

  set duration(val) {
    this.options.duration = val;
  }

  start() {
    const { start, duration, to } = this.options;
    this.animator = TweenLite.to({ ...start }, duration, {
      ...to,
      onUpdate: this.onUpdate,
      onUpdateParams: ["{self}"],
      onUpdateScope: this,
      onComplete: this.onComplete,
      onCompleteScope: this
    });
  }

  onUpdate(obj) {
    this.onUpdateFn(obj.target.percentage);
  }

  onComplete() {
    this.onCompleteFn();
  }
}
