import Animatible from './utils/Animatable';
import Vector from './utils/vector';
import { circlePath } from './utils/paths';

export default class Circle {
  constructor(x, y, size = 10, segments = 5) {
    this.animator = new Animatible(0.5, {
      onUpdate: this.onUpdate.bind(this),
      onComplete: this.onComplete.bind(this),
    });

    this.position = new Vector({ x, y });
    this.size = size;
    this.segments = segments;

    this.path = this._createPath();

    this.rotate = 0;
    this.scale = 0;
  }

  _createPath() {
    const circle = new circlePath(0, 0, this.size, this.segments);
    const path = new Path2D();

    path.moveTo(circle[0].x, circle[0].y);
    for (let i = 1; i < circle.length; i++) {
      path.lineTo(circle[i].x, circle[i].y);
    }
    path.lineTo(circle[0].x, circle[0].y);

    return path;
  }

  animate() {
    this.startRotate = this.rotate;
    this.startScale = this.scale;

    this.animating = true;
    this.animator.start();
  }

  onUpdate(percentage) {
    this.rotate = this.startRotate + ((Math.PI * 0.5) * percentage);
    this.scale = Math.sin(percentage * Math.PI);
  }

  onComplete() {
    this.animating = false;
  }

  render() {
    ctx.save();
    ctx.translate(this.position.x, this.position.y);
    ctx.rotate(this.rotate);
    ctx.scale(this.scale, this.scale);

    if (this.animating) {
      ctx.strokeStyle = 'red';
    } else {
      ctx.strokeStyle = 'black';
    }

    ctx.stroke(this.path);

    ctx.restore();
  }
}
